import firebase from 'firebase/app'
import { db } from '~/plugins/firebaseConfig.js'
import 'firebase/auth'
export default {
  data() {
    return {
      show1: false,
      email: '',
      password: '',
      error: '',
      name: '',
      lastname: '',
      date: '',
      ID: '',
    }
  },
  methods: {
    singup() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then((user) => {
          console.log(user)
          this.$router.push('/')
        })
        .catch((error) => {
          this.error = error
          alert(error)
          this.$router.push('/')
        })
      /* eslint no-var: */
      var data = {
        email: this.email,
        name: this.name,
        lastname: this.lastname,
        date: this.date,
        ID: this.ID,
      }
      db.collection('User')
        .doc(this.email)
        .set(data)
        .then(function () {
          alert('DONE')
        })
    },
  },
}
